import {Button, Select, Form, Input, DatePicker} from 'antd';
import React from 'react';
import calendar from "./calendar.svg"
import artwork1 from './Artwork1.svg';
import ellipse from './Ellipse.svg';
import ellipse1 from './Ellipse1.svg';
const { TextArea } = Input;
const { RangePicker } = DatePicker;
const dateFormat = 'DD/MM/YYYY';
const { Option } = Select;
const formItemLayout = {
  labelCol: {
    xs: {
      span: 10,
    },
    sm: {
      span: 5,
    },
  },
  wrapperCol: {
    xs: {
      span: 5,
    },
    sm: {
      span: 8,
    },
  },
};

const DataR = () => {
  const onFinish = (fieldsValue) => {
    console.log('Received values of form: ', fieldsValue);
  };

  return (
    <Form  {...formItemLayout} onFinish={onFinish} labelAlign='left' size='middle'>
      <Form.Item name="" label={<b></b>}>
        <Input placeholder="  ..." />
      </Form.Item>
      <div style={{margin: '-2vh'}}></div>
      <Form.Item name=" " label={<b> </b>}>
        <Input placeholder="1234" />
      </Form.Item>
      <div style={{margin: '-2vh'}}></div>
      <Form.Item name=" " label={<b> </b>}>
        <RangePicker
          placeholder={[('01/01/2022'), ('01/01/2022')]}
          format={dateFormat}
          style={{width: '100%',}}
          suffixIcon={<img src={calendar} alt=''/>}
          separator={'-'}
        />
      </Form.Item>
      <div style={{margin: '5vh'}}></div>
      <Form.Item name=" " label={<b> </b> }>
        <Select defaultValue={" "} suffixIcon={<img src={artwork1} alt=''/>}>
          <Option value=" "/>
          <Option value=" "/>
          <Option value=" "/>
        </Select>
       </Form.Item>
       <div style={{margin: '-2vh'}}></div>
      <Form.Item name="" label={<b></b>}>
        <Select defaultValue={" "} suffixIcon={<img src={artwork1} alt=''/>} prefix={<img src={artwork1} alt=''/>}>
          <Option value=" ">{<img src={ellipse} alt='' margin='0, 10vh'/>}  </Option>
          <Option value="  ">{<img src={ellipse1} alt=''/>}   </Option>
        </Select>

      </Form.Item>
      <div style={{margin: '5vh'}}></div>
      <Form.Item name="" label={<b></b>}>
        <TextArea placeholder="" allowClear style={{height: '20vh',}}/>
      </Form.Item>
      <Form.Item
        wrapperCol={{
          xs: {
            span: 10,
            offset: 0,
          },
          sm: {
            span: 13,
            offset: 0,
          },
        }}
      >
        <div style={{margin: '20vh'}}></div>
        <Button type="primary" ghost style={{width: '27vh', margin: '0 3vh', fontSize: '90%',}} htmlType="submit" size='large' color='green'></Button>
        <Button type="primary" htmlType="submit" style={{width: '51vh',  fontSize: '90%',}} size='large'>  </Button>
      </Form.Item>
    </Form>
  );
};

export default DataR;